___CSS_LOADER_EXPORT___.push([module.id, `body{
    background-color: rgb(41, 41, 41);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    gap: 50px;
    padding: 50px;
}
footer{
    text-align: center;
    font-style: italic;
}
section{
    background-color: rgb(97, 97, 97);
    border-radius: 15px;
    padding: 15px;
    margin: auto;
    box-shadow: 5px 5px 10px;
    width: 600px;
}
h1{
    display: inline-flex;
    padding: 3px 41px 2px 42px;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    background: #000;
    color: #baf781;
    font-family: Inter;
    font-size: 64px;
    font-weight: 400;
}
.result{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    font-size: 32px;
    color: #baf781;
    padding-bottom: 15px;
}
button{
    background-color: #000;
    color: #baf781;
    border: none;
    padding: 10px;
    border-radius: 5px;
    transition: 0.5s;
    cursor: pointer;
    margin-top: 5px;
    width: 100%;
    font-size: 16px;
}
button:hover{
    background-color: #baf781;
    color: #000;
}
.add{
    font-size: 20px;
}
.add input{
    margin-bottom: 10px;
}
.msg-error{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    font-size: 32px;
    color: rgb(255, 159, 159);
    padding-bottom: 15px;
}
